.app {
  text-align: center;
}

.app-header {
  background-color: #949494;
  height: 50px;
  padding: 20px;
  color: white;
}

.app-title {
  font-size: 1.5em;
  float: left;
}

.app-history {
  
}

.app-movies-text, .app-history-text {
  padding-left: 20px;
  float: left;
}

.app-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.app-movies-refresh-button, .app-movies-clear-button {
  cursor: pointer;
  float: right;
  padding: 20px 20px 0 0;
}

.app-movies-list, .app-watched-movies-list {
  display: -ms-flexbox;
  display: flex;
  overflow-x: scroll;
  padding: 10px 0;
}

@media (max-width: 768px) {
