        flex: 1 1;
  }
}

.app-movie-container {
  border: 1px solid transparent;
  min-width: 217px;
  padding: 10px;
  text-align: left;
}

.app-movie-container-onfocus {
  border: 1px solid black;
}

.app-movie-placeholder {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  min-width: 214px;
  min-height: 317px;
  margin-bottom: 10px;
}

.app-video {
  width: 0;
}


